import React, { useContext } from 'react';
import { Typography } from '@mui/material';
import { AppContext } from '../../../components/common/context/AppProvider';
import { NextSeo } from 'next-seo';
import Tasks from '../../../components/account/Worlds/World4/Rift/Tasks';
import Bonuses from '../../../components/account/Worlds/World4/Rift/Bonuses';
import SkillMastery from '../../../components/account/Worlds/World4/Rift/SkillMastery';
import ConstructMastery from '../../../components/account/Worlds/World4/Rift/ConstructMastery';
import Tabber from '../../../components/common/Tabber';
const Rift = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    rift,
    totalSkillsLevels,
    towers
  } = state?.account || {};
  let tabs = ['Tasks', 'Bonuses', 'Skill Mastery', 'Construct Mastery'].filter(tab => rift?.currentRift > 15 ? true : tab !== 'Skill Mastery').filter(tab => rift?.currentRift > 40 ? true : tab !== 'Construction Mastery');
  return <>
    <NextSeo title="Rift | Idleon Toolbox" description="Keep track of your rift bonuses, tasks and more" data-sentry-element="NextSeo" data-sentry-source-file="rift.jsx" />
    <Tabber tabs={tabs} data-sentry-element="Tabber" data-sentry-source-file="rift.jsx">
      <Tasks {...rift} characters={state?.characters} data-sentry-element="Tasks" data-sentry-source-file="rift.jsx" />
      <Bonuses {...rift} account={state?.account} data-sentry-element="Bonuses" data-sentry-source-file="rift.jsx" />
      <SkillMastery {...rift} totalSkillsLevels={totalSkillsLevels} characters={state?.characters} data-sentry-element="SkillMastery" data-sentry-source-file="rift.jsx" />
      <ConstructMastery {...rift} totalLevels={towers?.totalLevels} data-sentry-element="ConstructMastery" data-sentry-source-file="rift.jsx" />
    </Tabber>
  </>;
};
export default Rift;